<template>
    <div>
      <base-header title="평면정보" :mainImage="require('../assets/images/bg.jpg')" :tabs="tabs" @changeTab="changeType"/>
      <v-container fluid>
        <div v-if="currentType" class="col-md-12 col-sm-12 col-xs-12">
          <p class="display-1 mb-0 pt-5">{{ currentType.title }} 타입 평면도</p>
          <v-img class="align-end" :src="currentType.image"></v-img>
          <p class="pt-5 subtitle-1">
            {{ currentType.title }} 타입에 대해서 추가적인 설명을 추가해
            주세요.{{ currentType.title }} 타입에 대해서 추가적인 설명을 추가해
            주세요. ~~~~~{{ currentType.title }} 타입에 대해서 추가적인 설명을
            추가해 주세요.{{ currentType.title }} 타입에 대해서 추가적인 설명을
            추가해 주세요.{{ currentType.title }} 타입에 대해서 추가적인 설명을
            추가해 주세요.{{ currentType.title }} 타입에 대해서 추가적인 설명을
            추가해 주세요.{{ currentType.title }} 타입에 대해서 추가적인 설명을
            추가해 주세요.
          </p>
          <ul class="subtitle-1">
            <li>이런 것이 좋습니다.</li>
            <li>저런 것이 좋습니다.</li>
          </ul>
          <p class="subtitle-1 pt-5">
            이런식으로 설명을 추가할 수 있습니다.
          </p>
          <p class="display-1 mb-0 pt-5">설명</p>
          <p class="subtitle-1 pt-5">
            이런식으로 설명을 추가할 수 있습니다.
          </p>
        </div>
      </v-container>
    </div>
</template>

<script>
import BaseHeader from '@/components/layout/BaseHeader';

export default {
  components: {
    BaseHeader
  },
  data() {
    return {
      tabs: [
        {
          id: 'a',
        title: '59㎡',
        image: require('@/assets/images/type59a.png'),
        tabIcon: "mdi-format-list-bulleted-type",
        active: true
        },
        {
          id: 'b',
        title: '74㎡',
        image: require('@/assets/images/type74b.png'),
        tabIcon: "mdi-format-list-bulleted-type",
        active: false
        },
        {
          id: 'c',
        title: '84㎡',
        image: require('@/assets/images/type84b.png'),
        tabIcon: "mdi-format-list-bulleted-type",
        active: false
        },
      ],
      currentType: {}
    }
  },
  methods: {
    changeType(typeId) {
      this.tabs.forEach(t => {
        t.active = typeId == t.id;
      });
      this.currentType = this.tabs.find(t => t.active)
    }
  },
  mounted() {
    if (this.tabs) {
      let houseType = this.tabs[0];
      if (this.$route.params && this.$route.params.type) {
        houseType = this.tabs.find(t => t.id === this.$route.params.type)
      }
      this.changeType(houseType.id)
    }
  }
}
</script>



